import { Breadcrumbs, Button, ButtonGroup, Hidden, List, ListItem, ListItemSecondaryAction, ListItemText, Modal, Paper, Typography } from "@material-ui/core";
import { Link } from "gatsby";
import * as React from "react";
import { useUserInfo } from "shared/components/save";
import Defer from "shared/components/ui/Defer";
import Navbar from "shared/components/ui/Navbar";
import Ambulan from '@material-ui/icons/LocalHospital';
import Kematian from '@material-ui/icons/AirlineSeatFlat';
import Kecelakaan from '@material-ui/icons/Commute';
import Kerusakan from '@material-ui/icons/DomainDisabled';
import Kejahatan from '@material-ui/icons/Warning';
import Bencana from '@material-ui/icons/Pool';
import Call from '@material-ui/icons/Call';
import CallWa from '@material-ui/icons/CallRounded';
import { useModalTheme } from "shared/components/style";
import { useFetcher } from "shared/components/ajax";

const options = [{
    type: 'ambulan',
    label: 'Ambulan',
    desc: 'Layanan untuk memanggil ambulan desa untuk keperluan antar jemput',
    icon: Ambulan
}, {
    type: 'kematian',
    label: 'Kematian',
    desc: 'Layanan untuk menanggapi laporan warga yang meninggal dunia',
    icon: Kematian
}, {
    type: 'kecelakaan',
    label: 'Kecelakaan',
    desc: 'Layanan untuk merespon kecelakaan rumah tangga atau lalu lintas',
    icon: Kecelakaan
}, {
    type: 'kerusakan',
    label: 'Kerusakan',
    desc: 'Layanan untuk merespon kerusakan insfrasuktur publik',
    icon: Kerusakan
}, {
    type: 'kejatahan',
    label: 'Kejahatan',
    desc: 'Layanan untuk merespon tindak kejahatan di pemukiman warga',
    icon: Kejahatan
}, {
    type: 'bencana',
    label: 'Bencana',
    desc: 'Layanan untuk merespon bencana alam, longsor atau kebarakan',
    icon: Bencana
}
];

const formatTel = (str) => {
    return `tel:+62${str.substr(1)}`
}

const formatWa = (str) => {
    return `https://wa.me/62${str.substr(1)}`
}

const Prompt = ({ inquiry, open, onClose }) => {
    const user = useUserInfo();
    const modalTheme = useModalTheme();
    return (
        <Modal
            open={open}
            onClose={onClose}
        >
            <Paper className={modalTheme.paper} elevation={5} style={{ textAlign: 'center' }}>{open && <List>
                <ListItem className={modalTheme.center}>
                    {React.createElement(inquiry.category.icon, { style: { fontSize: '2em', marginRight: 15 } })}
                    <Typography>Layanan Darurat {inquiry.category.label}</Typography>
                </ListItem>
                <ListItem className={modalTheme.center}>
                    <Typography>Perangkat Desa {user.geolookup.kelurahan} memberikan data kontak berikut untuk penanganan layanan {inquiry.category.label}</Typography>
                </ListItem>
                {inquiry.contacts.map(x =>
                    <ListItem key={x.number}>
                        <ListItemText primary={x.name} secondary={x.number} />
                        <ListItemSecondaryAction>
                            <Hidden only={['xs']}>
                                <ButtonGroup >
                                    <Button component="a" href={formatTel(x.number)}>
                                        <Call style={{ marginRight: 10 }} />
                                        Telepon
                                    </Button>
                                    <Button component="a" disabled={!x.whatsapp} href={formatWa(x.number)} target="_blank">
                                        <CallWa style={{ marginRight: 10 }} />
                                        WhatsApp
                                    </Button>
                                </ButtonGroup>
                            </Hidden>
                            <Hidden smUp>
                                <ButtonGroup orientation="vertical" >
                                    <Button component="a" href={formatTel(x.number)}>
                                        <Call style={{ marginRight: 10 }} />
                                        Telepon
                                    </Button>
                                    <Button component="a" disabled={!x.whatsapp} href={formatWa(x.number)} target="_blank">
                                        <CallWa style={{ marginRight: 10 }} />
                                        WhatsApp
                                    </Button>
                                </ButtonGroup>
                            </Hidden>

                        </ListItemSecondaryAction>
                    </ListItem>)
                }
                <ListItem className={modalTheme.center}>
                    <Button onClick={onClose} variant="outlined">Tutup</Button>
                </ListItem>
            </List>}</Paper>
        </Modal>
    );
}

const Page = () => {
    const user = useUserInfo();
    const contactList = useFetcher(`/desa/emergency/`);
    const [inquiry, setInquiry] = React.useState(null);
    const confirm = (ctg) => {
        setInquiry({
            contacts: contactList.data.filter(x => x.service === options[ctg].type),
            category: options[ctg]
        });
    }
    return (
        <div>
            <Navbar />
            <List>
                <ListItem>
                    <Breadcrumbs>
                        <Link color="inherit" to="/user/">
                            Layanan Desa
                        </Link>
                        <Typography color="textPrimary">Layanan Darurat</Typography>
                    </Breadcrumbs>
                </ListItem>
                <Defer data={user && contactList}>
                    {() => (
                        user.features.emergency ?
                            <>
                                <ListItem>
                                    <Typography variant="h5">
                                        Layanan Darurat Desa {user.geolookup.kelurahan}
                                    </Typography>
                                </ListItem>
                                {
                                    options.map((x, k) => <ListItem disabled={!contactList.data.some(y => x.type === y.service)} key={x.type} button onClick={() => confirm(k)}>
                                        {React.createElement(x.icon, { style: { fontSize: '2em', marginRight: 15 } })}
                                        <ListItemText primary={x.label} secondary={x.desc} />
                                    </ListItem>)
                                }
                            </>
                            :
                            <ListItem>
                                <ListItemText primary={`Layanan Darurat untuk Desa ${user.geolookup.kelurahan} Belum Tersedia`} />
                            </ListItem>
                    )}
                </Defer>
                <Prompt open={inquiry !== null} onClose={() => setInquiry(null)} inquiry={inquiry} />
            </List>
        </div>
    );
}

export default Page
